
import { defineComponent } from 'vue'
import { userStore } from '../stores/user'

export default defineComponent({
	name: 'Login',
	data(): {
		email: string
		password: string
		message?: string
		submitting: boolean
	} {
		return {
			email: '',
			password: '',
			message: undefined,
			submitting: false,
		}
	},
	methods: {
		async submit(e: Event) {
			e.preventDefault()
			this.submitting = true
			this.message = undefined

			const res = await userStore.confirmEmail(this.$route.params.token as string, {
				email: this.email,
				password: this.password,
			})

			this.submitting = false
			if (res.error) {
				this.message = res.error.message
			} else {
				this.message = res.message
				this.email = ''
				this.password = ''
			}
		},
	},
})
